.language_selector_image_map {
    height: 140px;
    width: 90%;

    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 5px;

    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 5px;
    box-shadow: 1px 1px 3px 0px grey;
}
.language_selector_image_map img {
    height: 200%;
    width: 100%;
}
.language_selector_wrapper {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;

    display: flex;
    flex-direction: column;

    font-family: TeleGrotesk,'Helvetica Neue',Helvetica,Arial,'Lucida Grande',sans-serif;
    font-size: 1.15rem;
    font-weight: bold;

    user-select: none;
}
.language_selector_row {
    height: 55px;
    width: 100%;
    margin-top: 7px;

    display: flex;
    align-items: center;
    cursor: pointer;

    background-color: #e6e6e6;

    border: 1px solid #e6e6e6;
    border-radius: 5px;
    box-shadow: 1px 1px 3px 0px grey;

    transition: transform .5s;
}
.language_selector_row:hover {
    background-color: #ffe6e6;
    border: 1px solid grey;
    transform: scale(1.02);
}
.language_selector_row_selected {
    height: 55px;
    width: 100%;
    margin-top: 5px;

    display: flex;
    align-items: center;

    background-color:  #ccffcc;
    border-radius: 5px;
    box-shadow: 1px 1px 3px 0px grey;
    border: 1px solid grey;
}
.language_selector_row_image {
    height: 50px;
    width: 50px;

    margin-left: 15px;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: clip;

    border-radius: 50%;
}
.language_selector_row_image img {
    height: 100%;
    width: auto;
    transition: transform .5s;
}
.language_selector_row_image img:hover {
    transform: scale(1.20);
}
.language_selector_row_label {
    height: 50px;
    width: 50%;

    padding-left: 20px;
    display: flex;
    align-items: center;
}
.language_title {
    margin: 3%;
    color: green;
    font-family: 'Grinched';
    font-size: 2.5rem;
    text-align: center;
    text-shadow: '1px 1px 2px black';
    line-height: 0.9;
}
.language_selector {
    margin: 5%;
    padding-left: 7%;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    border-radius: 15px;
}
.language_selector_btn {
    margin: 5%;
    display: flex;
    flex-direction: column;

    font-weight: bold;
    font-family: TeleGrotesk,'Helvetica Neue',Helvetica,Arial,'Lucida Grande',sans-serif;
}
.language_selector_warning {
    margin: 5%;

    display: flex;
    align-items: center;
    justify-content: center;

    font-family: TeleGrotesk,'Helvetica Neue',Helvetica,Arial,'Lucida Grande',sans-serif;
    font-size: 1rem;
    font-weight: bold;
    color: red;
}

@media only screen and (max-width: 800px) {
    .language_selector_image_map {
        width: 96%;
        margin-left: 2%;
        margin-right: 2%;
        margin-bottom: 5px;
    }
    .language_selector_wrapper {
        width: 96%;
        margin-left: 2%;
        margin-right: 2%;
        font-size: 1.15rem;
    }
    .language_selector_row_label {
        height: 50px;
        width: 70%;
        padding-left: 20px;
        display: flex;
        align-items: center;
    }
    .language_selector_btn {
        margin: 2%;
    }
}
@media only screen and (max-height: 400px) {
    .language_selector_image_map {
        display: none;
    }
    .language_selector_wrapper {
        font-size: 0.95rem;
    }
    .language_selector_row {
        height: 45px;
        margin-top: 7px;
    }
    .language_selector_row_selected {
        height: 45px;
    }
    .language_selector_row_image {
        height: 40px;
        width: 40px;
    }
    .language_selector_btn {
        margin-top: 1%;
    }
}
